import React, { Component } from 'react';
import '../css/play.css'
import '../css/animate.min.css'
import $ from 'jquery';
class Play extends React.Component {
    constructor() {
        super();
        this.n=1
        this.angles = 0
        this.flag = false
        this.error=false
        this.state = {
            list: [
                { song: "起风了", singer: "买辣椒也用券" },
                { song: "Turnin", singer: "Young Rising Sons" },
                { song: "Fairy Tale", singer: "Michael Learns To Rock" },
                { song: "归去来兮", singer: "叶炫清" },
                { song: "耳朵", singer: "李荣浩" },
                { song: "没说什么", singer: "张杰" },
                { song: "爱无界", singer: "刘惜君" },
                { song: "那一夜", singer: "邓紫棋" },
                { song: "为你写诗", singer: "汪苏泷/周洁琼" },
                { song: "梦不落雨林", singer: "张艺兴" },
                { song: "时空行者", singer: "汪苏泷" },
                { song: "恰好", singer: "A-lin" },
                { song: "我知道是你", singer: "李荣浩" }
            ],
            new: [
                "http://111.202.85.143/amobile.music.tc.qq.com/C4000004jPDk2eB2dt.m4a?guid=2114058200&vkey=A700B01ECD233F6335249FB3A2AED3DC9C34CC17569047F3BAE9616DE69C6991673C339CBE05FC15E3E4D18335814D5191EDC71E883D4B0E&uin=0&fromtag=38'",
                "http://dl.stream.qqmusic.qq.com/C400000R98XB3XzhxZ.m4a?guid=2114058200&vkey=CF271E3FA5E29AE0F0200EAF2262A00319B5C967883CF983AB12F25F7971E28C30875CA24CDD0484E502F770322A2864684CDF35CD11D5B6&uin=0&fromtag=38",
                "http://dl.stream.qqmusic.qq.com/C4000040fn1W1WM0YR.m4a?guid=2114058200&vkey=A4A063B4DAD140E920BC8E4E7086CCDC856AC91085623222702B470547A64B818E3FCE8412C51DA4CD879B429BECEB350B0AC60DFA5E3DC9&uin=0&fromtag=38",
                "http://111.202.98.159/amobile.music.tc.qq.com/C4000017W9Hf0YeeqG.m4a?guid=2114058200&vkey=E78936ADB0F1A99DBB46036F4091622FE7D52B25D09AC4FE469E94CC77936E1D019E5B892D073057FAF6243A024F36482CA032490455E973&uin=7544&fromtag=66", "http://isure.stream.qqmusic.qq.com/C400001dPKD40OUxFz.m4a?guid=2114058200&vkey=FE9D9187DDD0021E4D406EC7B90A9EDFA3DD840700B0CF82F82E79EBBD1A5400FC0B7E1189E33D0181F5B88D8543650AAE181602A5D57756&uin=7544&fromtag=66", "http://isure.stream.qqmusic.qq.com/C400002TWvNU1Oj5sV.m4a?guid=2114058200&vkey=0C82D4A4CF88426FE45FC11ECF7ADDD2DC3DFA740F8E63FF05DE0240698A92D29FBF18F97CBDDB47407ED0E12854B2CEE5B421227C64D430&uin=7544&fromtag=66", "http://isure.stream.qqmusic.qq.com/C400004SUGlZ3eEjrO.m4a?guid=2114058200&vkey=87015989C2C2541A0CD6F657F535059B7F8961A2C27AE349EA7558700104E57B44C94D912D6DC71E901F229F4590FBC4F0B26163E565D164&uin=7544&fromtag=66", "http://isure.stream.qqmusic.qq.com/C400100zBURs2OgW6n.m4a?guid=2114058200&vkey=B50471B73EEA14A00022BE4AC72A5484FC8C8DBD41A5E3F7D75092ADAB54EE861825C99FDD4B4665EFFB6AE784C9C473433737E696482A4B&uin=7544&fromtag=66", "http://isure.stream.qqmusic.qq.com/C400001ulpfc4XLL5k.m4a?guid=2114058200&vkey=91E2BB3C85DE4612DEAD3776BDFD9535A7282E63E36740C2E2EF9BA59AED968974105A280054C9133C4F24375E729E92046559D955B2C232&uin=7544&fromtag=66", "http://isure.stream.qqmusic.qq.com/C400102adHlS1J0548.m4a?guid=2114058200&vkey=D03A4524CDD406A5B16CDE06EE5DFEDE0D5DD3B0AEF4214048655BC3F4C0E6CDBBFD9DE1659E8620ABDCD659D35A79B7FA32A49E2B97145E&uin=7544&fromtag=66", "http://isure.stream.qqmusic.qq.com/C400000LIT9R0DTWbN.m4a?guid=2114058200&vkey=FCD130E5784F423D43CC9A59C650AE50723A4C7CE62802FA0AEA9185707C6642AF04967DD3F0229BFDB5C960E05615D50A95CA346A5DC190&uin=7544&fromtag=66", "http://isure.stream.qqmusic.qq.com/C400003sOOX20m4QGb.m4a?guid=2114058200&vkey=50B75823F743D1B236420A101EAF7E5265ACF621A37FE9D5B0528BBDAA98A7681B9A945590B93E56281112525482047C1032151B51C27A13&uin=7544&fromtag=66", "http://isure.stream.qqmusic.qq.com/C4000011aHDq1HVpUW.m4a?guid=2114058200&vkey=6D13F2AEC8AA457FD4F4EB9BAE07FBA1090B1202128C9F68A3A2BE1FE05AFA89149CE1165FD6894D4034E8C1711E0723320DCFF397C9FF0B&uin=7544&fromtag=66"
            ]
        }
    }
    componentDidMount() {  
        clearInterval(window.timer) 
        const list = this.props.match.params.list;
        const id = this.props.match.params.id;
        $('audio').attr('src', this.state[list][id]);
        setTimeout(()=>{
            this.pause()
        },800)
    }
    end() {
        this.pause();
    }
    pause() {
        if( this.error){return false}
        if (this.flag) {
            $('#ad')[0].pause();
            this.flag = false;
            $('.disc_box .icon').toggle();
            clearInterval(window.timer)
        } else {
            $('#ad')[0].play();
            this.flag = true;
            $('.disc_box .icon').toggle();
            this.run()
        }
    }
    err(){
        alert('资源加载失败!')
        this.error=true
        this.pause();
    }

    run() {
        window.timer = setInterval(() => {
            console.log(window.timer)
            this.angles++
            $('.running').css('transform', "rotate(" + (this.angles) + "deg)");
        }, 20)
    }
    render() {
        return <div className='play_box animated  bounceInUp'>
            <div className='op_box'>
                <audio id='ad' src='' onEnded={this.end.bind(this)} onError={this.err.bind(this)}></audio>
                <span className='logo'></span>
                <div class="m-song-disc">
                    <img className='l' src="../../img/needle-plus.png" />
                    <div className="disc_box">
                        <img className='pic running' src="../../img/default_album.jpg" />
                        <img className='icon' src="../../img/play_btn_3x.png" />
                        <img className='disc running' src="../../img/disc-plus.png" onClick={this.pause.bind(this)} />
                    </div>
                </div>
            </div>
            <div className='music_msg'>
                {this.state.list[this.props.match.params.id].song}<span> -- {this.state.list[this.props.match.params.id].singer}</span>
            </div>
        </div>
    }
}
export default Play