import React  from 'react';
import {Route,Switch} from 'react-router-dom';

import Home from './components/home'
import Hot from './components/hot'
import Search from './components/search'
import Play from './components/play'
class Router extends React.Component{
    render(){
        return <div className='cont'>
            <Switch>
                <Route exact path='/react_demo_wyymusic' component={Home}/>
                <Route  path='/react_demo_wyymusic/hot' component={Hot}/>
                <Route path='/react_demo_wyymusic/search' component={Search}/>
                <Route path='/react_demo_wyymusic/play/:list/:id' component={Play}/>
            </Switch>
        </div>
    }
}
export default Router