import React, { Component } from 'react';
import '../css/hot.css';
import Footer from './footer'
import { NavLink } from 'react-router-dom';
class Hot extends React.Component {
    constructor() {
        super();
        this.state = {
            hot: [
                { song: "起风了", singer: "买辣椒也用券" },
                { song: "Turnin", singer: "Young Rising Sons" },
                { song: "Fairy Tale", singer: "Michael Learns To Rock" },
                { song: "归去来兮", singer: "叶炫清" },
                { song: "耳朵", singer: "李荣浩" },
                { song: "没说什么", singer: "张杰" },
                { song: "爱无界", singer: "刘惜君" },
                { song: "那一夜", singer: "邓紫棋" },
                { song: "为你写诗", singer: "汪苏泷/周洁琼" },
                { song: "梦不落雨林", singer: "张艺兴" },
                { song: "时空行者", singer: "汪苏泷" },
                { song: "恰好", singer: "A-lin" },
                { song: "我知道是你", singer: "李荣浩" }
            ]
        }
    }
    render() {
        return <div className='warp'>
            <div className='big_pic'>
                <div className="title"></div>
                <p className="time">更新日期:<span>10月25日</span></p>
            </div>
            <ul className='list'>
                {this.state.hot.map((item, index) => {
                    return <NavLink className="li" to={'/react_demo_wyymusic/play/new/' + index}>
                        <div>{index > 8 ? (index + 1) : '0' + (index + 1)}</div>
                        <div className='lists'>
                            <p className="song">{item.song}</p>
                            <p className="singer"><span ></span>{item.singer}</p>
                        </div>
                        <p className="play_icon_r"></p>
                    </NavLink>
                })}
            </ul>
            <Footer/>
        </div>
    }
}
export default Hot